.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

.App-main {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.container {
  display: flex;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

.sidebar {
  flex: 1;
  background-color: #f4f4f4;
  padding: 20px;
  border-right: 2px solid #ddd;
}

.basket {
  flex: 2;
  padding: 20px;
}

.item-list {
  list-style-type: none;
  padding: 0;
}

.item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.item input {
  width: 60px;
}

.calculate-button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.calculate-button:hover {
  background-color: #0056b3;
}

.discounts {
  margin-top: 20px;
  text-align: left;
}

.App-footer {
  background-color: #282c34;
  color: white;
  padding: 10px;
  position: fixed;
  width: 100%;
  bottom: 0;
}
